
import { defineComponent, computed, ref, watch } from 'vue'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    props:['langData'],
    setup(props) {
        const qf: any = new QuickFetch();
        const lang: any = computed(()=>props.langData);
        const unified_id: any = ref('');
        const school_id: any = ref('');
        const name: any = ref('');
        const phone: any = ref('');
        const password: any = ref('');
        const confirm_password: any = ref('');
        const password_match: any = ref(true);
        function passwordMatch(){
            if(password.value.length>0 && confirm_password.value.length>0){
                if(password.value == confirm_password.value){
                    password_match.value = true;
                }else{
                    password_match.value = false;
                }
            }else{
                password_match.value = true;
            }
        }
        watch(password,()=>{
            passwordMatch();
        });
        watch(confirm_password,()=>{
            passwordMatch();
        });
        function submitAll(){
            if(qf.EptObj(unified_id)){
                alert("請輸入身分證字號");
            }else if(qf.EptObj(school_id)){
                alert("請輸入學號 / 員工編號");
            }else if(qf.EptObj(name)){
                alert("請輸入真實姓名");
            }else if(qf.EptObj(phone)){
                alert("請輸入手機號碼");
            }else if(qf.EptObj(password)){
                alert("請輸入密碼");
            }else if(qf.EptObj(confirm_password)){
                alert("請再輸入一次密碼");
            }else if(password_match.value == false){
                alert("兩次輸入密碼不相同");
            }else{
                qf.Dataset().AddObj('unified_id',unified_id).AddObj('school_id',school_id).AddObj('name',name).AddObj('phone',phone).AddObj('password',password);
                qf.Url('nhc-auth/signup').Login(qf.GetDset()).then((res: any)=>{
                    if(res.message=='done'){
                        alert("完成註冊，請登入系統");
                    }else{
                        alert("註冊失敗，請洽系統管理員");
                    }
                })
            }
        }
        return{
            lang,
            unified_id,
            school_id,
            name,
            phone,
            password,
            confirm_password,
            password_match,
            submitAll,
        }
    },
})
