
import { defineComponent, computed, ref } from 'vue'
import { QuickFetch } from '@/components/quick'
import { useRouter } from 'vue-router'

export default defineComponent({
    props:['langData'],
    setup(props) {
        const qf: any = new QuickFetch();
        const router = useRouter();
        const lang: any = computed(()=>props.langData);
        const unified_id: any = ref('');
        const school_id: any = ref('');
        const password: any = ref('');
        function submitAll(){
            if(qf.EptObj(unified_id)){
                alert("請輸入身分證字號");
            }else if(qf.EptObj(school_id)){
                alert("請輸入學號 / 員工編號");
            }else if(qf.EptObj(password)){
                alert("請輸入密碼");
            }else{
                qf.Dataset().AddObj('unified_id',unified_id).AddObj('school_id',school_id).AddObj('password',password);
                qf.Url('nhc-auth/login').Login(qf.GetDset()).then((res: any)=>{
                    if(res.message=='done'){
                        localStorage.nhc_token = res.token.token;
                        localStorage.nhc_data = JSON.stringify(res.token);
                        router.push('/');
                    }else{
                        alert("登入失敗，請洽系統管理員");
                    }
                })
            }
        }
        return{
            lang,
            unified_id,
            school_id,
            password,
            submitAll,
        }
    },
})
